import NextAuth, { AuthOptions } from "next-auth"
import { PrismaAdapter } from "@auth/prisma-adapter"
import { db } from "@/lib/db"
import GoogleProvider from "next-auth/providers/google"
import { ROLES, RoleType } from "@/lib/constants"

// Domain email ƒë∆∞·ª£c ·ªßy quy·ªÅn cao h∆°n
const AUTHORIZED_TEACHER_DOMAINS = ['school.edu', 'university.edu', 'teacher.org']

// Fix cho type adapter
const prismaAdapter = PrismaAdapter(db) as any

// C·∫£i thi·ªán error handling
const handleAuthError = (error: any, context: string) => {
  console.error(`[Auth Error] ${context}:`, error);
  return false;
}

export const authOptions: AuthOptions = {
  adapter: prismaAdapter,
  providers: [
    GoogleProvider({
      clientId: process.env.GOOGLE_CLIENT_ID!,
      clientSecret: process.env.GOOGLE_CLIENT_SECRET!,
      allowDangerousEmailAccountLinking: true,
    }),
  ],
  session: {
    strategy: "jwt",
    maxAge: 24 * 60 * 60, // 24 hours
    updateAge: 4 * 60 * 60, // 4 hours
  },
  callbacks: {
    async jwt({ token, user, account, profile, trigger }) {
      try {
        // Tr∆∞·ªùng h·ª£p ƒëƒÉng nh·∫≠p - Ch·ªâ c·∫≠p nh·∫≠t token khi c√≥ user t·ª´ ƒëƒÉng nh·∫≠p
        if (user) {
          // ƒê·∫£m b·∫£o role c√≥ th·ªÉ null
          token.role = user.role as RoleType | null;
          token.id = user.id;
          return token;
        }
        
        // Tr∆∞·ªùng h·ª£p trigger update - C·∫≠p nh·∫≠t token khi c√≥ s·ª± thay ƒë·ªïi r√µ r√†ng
        if (trigger === 'update' && token?.sub) {
          try {
            const dbUser = await db.user.findUnique({
              where: { id: token.sub },
              select: { 
                id: true, 
                name: true, 
                email: true, 
                role: true, 
                image: true 
              }
            });
            
            if (dbUser) {
              // C·∫≠p nh·∫≠t token v·ªõi gi√° tr·ªã m·ªõi
              token.role = dbUser.role as RoleType | null;
              token.name = dbUser.name;
              token.email = dbUser.email;
              token.picture = dbUser.image;
              
              // Th√™m timestamp ƒë·ªÉ ƒë·∫£m b·∫£o kh√¥ng cache token c≈©
              token.updatedAt = Date.now();
            }
          } catch (error) {
            console.error("Error updating token:", error);
          }
          return token;
        }
        
        // Tr∆∞·ªùng h·ª£p token t·ª´ session: ki·ªÉm tra n·∫øu c·∫ßn c·∫≠p nh·∫≠t
        if (token?.sub && (!token.role || !token.updatedAt || Date.now() - (token.updatedAt as number || 0) > 60 * 1000)) {
          try {
            const dbUser = await db.user.findUnique({
              where: { id: token.sub },
              select: { role: true }
            });
            
            if (dbUser) {
              token.role = dbUser.role as RoleType | null;
              token.updatedAt = Date.now();
            }
          } catch (error) {
            console.error("Error refreshing token:", error);
          }
        }
        
        return token;
      } catch (error) {
        console.error("JWT callback error:", error);
        return token;
      }
    },
    async session({ session, token }) {
      try {
        if (session.user && token) {
          // ƒê·∫£m b·∫£o role c√≥ th·ªÉ null
          session.user.role = token.role as RoleType | null;
          session.user.id = token.id as string;
        }
        return session;
      } catch (error) {
        console.error("Session callback error:", error);
        return session;
      }
    },
    async signIn({ user, account, profile }) {
      // N·∫øu user c√≥ email
      if (user?.email) {
        try {
          // Ki·ªÉm tra user ƒë√£ t·ªìn t·∫°i ch∆∞a
          const existingUser = await db.user.findUnique({
            where: { email: user.email },
          })
          
          if (!existingUser) {
            // T·∫°o user m·ªõi kh√¥ng c√≥ role (null) ƒë·ªÉ ng∆∞·ªùi d√πng ch·ªçn sau
            await db.user.create({
              data: {
                id: user.id,
                email: user.email,
                name: user.name,
                image: user.image,
                role: null, // Kh√¥ng thi·∫øt l·∫≠p role m·∫∑c ƒë·ªãnh
              },
            })
            
            // ƒê√°nh d·∫•u ƒë·ªÉ redirect t·ªõi trang ch·ªçn role
            return true
          }
          
          return true
        } catch (error) {
          return handleAuthError(error, "signIn callback");
        }
      }
      
      return true
    }
  },
  pages: {
    signIn: '/auth/signin',
    signOut: '/',
    error: '/auth/error',
    newUser: '/auth/select-role' // ƒêi·ªÅu h∆∞·ªõng ng∆∞·ªùi d√πng m·ªõi ƒë·∫øn trang ch·ªçn role
  },
  secret: process.env.NEXTAUTH_SECRET,
  debug: process.env.NODE_ENV === "development",
  logger: {
    error(code, ...message) {
      console.error(`[NextAuth Error] ${code}:`, ...message);
    },
    warn(code, ...message) {
      if (process.env.NODE_ENV === "development") {
        console.warn(`[NextAuth Warning] ${code}:`, ...message);
      }
    },
    debug(code, ...message) {
      if (process.env.NODE_ENV === "development") {
        console.debug(`[NextAuth Debug] ${code}:`, ...message);
      }
    },
  },
}

const handler = NextAuth(authOptions)
export { handler as GET, handler as POST }
